@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
.rd-rows{display:flex;flex-direction:column}*{box-sizing:border-box;margin:0;padding:0}body{font-family:"Oswald",sans-serif;overflow-y:scroll;padding-left:25px;padding-right:25px}main{margin-top:25px;margin-bottom:25px}header{position:sticky;top:0;display:flex;align-items:center;grid-gap:25px;gap:25px;height:100px;padding-top:25px;padding-bottom:25px;border-bottom:1px solid gray;background:#fff}header img{height:50px}header h1{margin-right:auto}header nav{display:flex;grid-gap:20px;gap:20px}header nav a{text-decoration:none}header nav .active{text-decoration:underline}.rd-rows{grid-gap:25px;gap:25px}.profiles{display:flex;flex-direction:row;grid-gap:25px;gap:25px}.profiles .profile-column{display:flex;flex-direction:column;width:33.3%}.profiles .profile-column ul{border:1px solid gray;border-radius:5px;margin-top:25px;margin-bottom:25px}.profiles .profile-column li{list-style:none;padding:10px}.profiles .profile-column li+li{border-top:1px solid gray}
